import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import './layout.css';

export const SocialMediaContainer = styled.ul`
  display: flex;
`;

const StyledSocialMediaIcon = styled.li`
  padding-right: 10px;
`;

class SocialMediaIcon extends React.Component {
  render() {
    return (
      <StyledSocialMediaIcon {...this.props}>
        <Link to={this.props.url}>
          <icon>
            <img
              src={this.props.src}
              alt={this.props.name ? this.props.name : ' '}
            />
          </icon>
        </Link>
      </StyledSocialMediaIcon>
    );
  }
}

export default SocialMediaIcon;

SocialMediaIcon.propTypes = {
  src: PropTypes.string.required,
  url: PropTypes.string.required,
  name: PropTypes.string
};
